<template>
    <div class="history-data card" ref="chart"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    mounted() {
      this.initChart();
    },
    methods: {
      initChart() {
        const chart = echarts.init(this.$refs.chart);
        chart.setOption({
          title: {
            text: '产量历史数据',
            left: 'center',
            textStyle: {
              color: 'white'
            }
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data:['最高产量','平均产量','最低产量'],
            orient: 'vertical',
            textStyle: {
              color: 'white',
              fontSize: 14, //设置图例文字大小
            },
            x:'left',      //可设定图例在左、右、居中
            y:'center',     //可设定图例在上、下、居中
            padding:[0,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离]
          },
          xAxis: {
            type: 'category',
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
            axisLabel: {
              color: 'rgba(255, 255, 255, 0.7)'
            }
          },
          yAxis: {
            type: 'value',
            yAxisMin: 0, // 设置最小值为0，确保数据不会超出图表范围
            yAxisMax: 230, // 设置最大值为200，确保数据不会超出图表范围
            axisLabel: {
              color: 'rgba(255, 255, 255, 0.7)'
            }
          },
          series: [{
            name: '最低产量',
            data: [40, 80, 120, 80, 60, 110, 130],
            type: 'line',
            lineStyle: {
              shadowColor: 'rgba(0, 0, 0, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 10,
              color: '#CBE799',
            },
            itemStyle: {
              color: '#CBE799',
            }
          },
          {
            name: '平均产量',
            data: [150, 145, 155, 120, 75, 160, 215],
            type: 'line',
            lineStyle: {
              color: 'rgba(0, 255, 0, 0.9)',
              shadowColor: 'rgba(0, 0, 0, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 10
            },
            itemStyle: {
              color: 'rgba(0, 255, 0, 0.9)',
            }
          },
          {
            name: '最高产量',
            data: [260, 210, 190, 160, 90, 210, 300],
            type: 'line',
            lineStyle: {
               color: '#FFA726',
              shadowColor: 'rgba(0, 0, 0, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 10
            },
            itemStyle: {
              color: '#FFA726'
            }
          },
        ]
        });
      }
    }
  };
  </script>
  
  <style scoped>
  .history-data {
    width: 100%;
    height: 300px;
  }
  </style>