<template>
        <div class="video-player-list">
          <component
            v-for="(video, index) in videoList"
            :key="index"
            :is="'VideoPlayer'"
            :videoUrl="video.url"
            class="video-player-item"
          />
        </div>
  </template>
  
  <script>
  import VideoPlayer from './VideoPlayer.vue';
  
  export default {
    name: 'Layout',
    components: {
      VideoPlayer
    },
    props: {
      videoList: {
        type: Array,
        required: true // 必须传入播放器列表
      }
    }
  };
  </script>
  
  <style scoped>

  .video-player-list {
    width: 100%; /* 确保播放器列表宽度与页面宽度一致 */;
    display: flex;
    justify-content: space-between; /* 水平均匀分布 */
    gap: 20px; /* 播放器之间的间距 */
  }
  
  .video-player-item {
    width: 100%; /* 计算宽度，减去间距 */
    height: 100%; /* 自适应高度 */
  }
  </style>