<template>
  <div class="data-display">
    <div v-for="(item, index) in dataItems" :key="index" class="data-card">
      <div class="data-label">{{ item.label }}</div>
      <div class="data-value-container">
        <div v-for="(digit, digitIndex) in formattedValue(item.value)" :key="digitIndex" class="digit-card">
          {{ digit }}
        </div>
    </div>
    </div>
  </div>
  </template>

<script>
import * as echarts from 'echarts';

export default {
  data() {
    return {
      dataItems: [
        { label: '叶片数', value: 0 }, // 初始值为0
        { label: '叶面积', value: 0 } ,// 初始值为0
        { label: '二氧化碳浓度', value: 0 }, // 初始值为0
        { label: '产量', value: 0 }, // 初始值为0
        { label: '效率', value: 0 }, // 初始值为0
        { label: '能耗', value: 0 }, // 初始值为0
      ],
      timers: [],

    };
  },
  props: {
    yAxisMin: {
      type: Number,
      default: 0
    },
    yAxisMax: {
      type: Number,
      default: 200
    }
  },
  mounted() {
    console.log('Chart DOM:', this.$refs.chart); 
    this.dataItems.forEach(item => {
      this.startRandomUpdateForItem(item); // 对每个数据项启动独立的更新逻辑
    });
  },
  beforeDestroy() {
    if (this.timers) {
      this.timers.forEach(timer => clearInterval(timer)); // 清除所有定时器，防止内存泄漏
    }
  },
  methods: {
    startRandomUpdateForItem(item) {
      const interval = Math.floor(Math.random() * 3000) + 1000; // 每隔1到4秒触发一次随机增加

      const timer = setInterval(() => {
        const randomIncrement = Math.floor(Math.random() * 100); // 随机增加值 (0-99)
        this.animateValueIncrease(item, randomIncrement); // 调用动画方法
      }, interval);

      if (!this.timers) {
        this.timers = [];
      }
      this.timers.push(timer); // 记录定时器以便在组件销毁时清除
    },
    animateValueIncrease(item, increment) {
      const steps = 50; // 分成50步完成
      const stepValue = increment / steps; // 每步增加的值
      let currentStep = 0;

      const animationTimer = setInterval(() => {
        if (currentStep < steps) {
          item.value += Math.floor(stepValue); // 每步增加
          currentStep++;
        } else {
          item.value = Math.floor(item.value); // 确保最终值是整数
          clearInterval(animationTimer); // 停止动画
        }
      }, 20); // 每20ms更新一次
    },
    // 格式化数值为数组，便于逐位显示
    formattedValue(value) {
      return value.toString().split('');
    }
  }
};
</script>

<style scoped>
/* 数据展示整体容器 */
.data-display {
  display: flex;
  justify-content: space-around; /* 数据项水平分布 */
  align-items: center;           /* 垂直居中对齐 */
  flex-wrap: wrap;               /* 允许换行 */
  gap: 20px;                     /* 数据项之间的间距 */
}

/* 数据卡片样式 */
.data-card {
  background-color: rgba(255, 255, 255, 0.2); /* 半透明背景 */
  backdrop-filter: blur(10px);                /* 模糊背景效果 */
  border-radius: 15px;                        /* 圆角 */
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);  /* 阴影效果 */
  padding: 20px;                              /* 内边距 */
  width: 200px;                               /* 固定宽度 */
  text-align: center;                         /* 文本居中 */
  animation: fade-in 1s;                      /* 淡入动画 */
  font-size: 24px;                            /* 字体大小 */
                  
}

/* 数据标签样式 */
.data-label {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8); /* 浅白色字体 */
  margin-bottom: 10px;             /* 标签与数值的间距 */
}

/* 数据数值容器样式 */
.data-value-container {
  display: flex;
  gap: 5px;
  align-content: center;
  justify-content: center;
}

/* 数据数值卡片样式 */
.digit-card {
  width: 40px;
  height: 40px;
  background-color: #447269;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-size: 24px;
}

/* 动画效果 */
@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
</style>