<template>
    <div class="video-player-container">
      <video ref="videoPlayer" class="video-player" controls></video>
    </div>
  </template>
  
  <script>
  import Hls from 'hls.js';
  
  export default {
    name: 'VideoPlayer',
    props: {
      videoUrl: {
        type: String,
        required: true // 必须传入 .m3u8 视频地址
      }
    },
    mounted() {
      this.initializePlayer();
    },
    beforeDestroy() {
      this.destroyPlayer();
    },
    methods: {
      initializePlayer() {
        const videoElement = this.$refs.videoPlayer;
        if (Hls.isSupported()) {
          const hls = new Hls();
          hls.loadSource(this.videoUrl); // 加载 .m3u8 视频地址
          hls.attachMedia(videoElement); // 将视频元素与 HLS 实例绑定
          hls.on(Hls.Events.MANIFEST_PARSED, () => {
            console.log('HLS manifest parsed, video ready to play.');
            videoElement.play(); // 自动播放（可选）
          });
        } else if (videoElement.canPlayType('application/vnd.apple.mpegurl')) {
          // Safari 原生支持 HLS
          videoElement.src = this.videoUrl;
          videoElement.addEventListener('loadedmetadata', () => {
            console.log('Native HLS support detected, video ready to play.');
            videoElement.muted = true; // 静音（可选）
            videoElement.play(); // 自动播放（可选）
          });
        } else {
          console.error('Your browser does not support HLS or native .m3u8 playback.');
        }
      },
      destroyPlayer() {
        const videoElement = this.$refs.videoPlayer;
        if (videoElement) {
          videoElement.pause();
          videoElement.src = ''; // 清空视频源
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .video-player-container {
    width: 100%;
  /* 固定最大宽度 */
    margin: 0 auto; /* 居中对齐 */
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px; /* 圆角 */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); /* 阴影效果 */
  }
  
  .video-player {
    width: 100%; /* 宽度占满容器 */
    height: auto; /* 自适应高度 */
    border-radius: 10px; /* 圆角 */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* 阴影效果 */
    background-color: rgba(0, 0, 0, 0.8); /* 背景颜色 */
  }
  </style>