<template>
  <div class="dashboard">
    <!-- 主标题 -->
    <h1 class="main-title" @click="goFullscreen">德州财金智慧农业科技有限公司</h1>
    <h2 class="sub-title">生产大脑</h2>
    <!-- 内容区域 -->
    <div class="content">
      <!-- 左侧输入区域 -->
      <div class="input-side card">
        <h3 style="color: rgba(255, 255, 255, 1);">输入区域</h3>      
        <div v-for="(input, index) in inputs" :key="index" class="input-group">
          <label :for="'input-' + index">{{ input.label }}</label>
          <el-input-number
            v-model="input.value"
            :min="0"
            :max="1000"
            :placeholder="input.placeholder"
            style="width: 150px;" 
          ></el-input-number>
      </div>
      
      </div>

      <!-- 中间图片 -->
      <div class="center-image card">
        <img src="@/assets/MainPic.png" alt="Center Image">
      </div>

      <!-- 右侧输出区域 -->
      <div class="output-side card">
        <h3 style="color: rgba(255, 255, 255, 1);">输出区域</h3>
        <p style="color: rgba(255, 255, 255, 0.8);">周总产量: {{ outputValue *11 }}</p>
        <p style="color: rgba(255, 255, 255, 0.8);">周每平米产量: {{ outputValue * 15 + 3 }}</p>
        <p style="color: rgba(255, 255, 255, 0.8);">采收工人数量: {{ outputValue  * 14 + 6 }}</p>
      </div>

      <!-- 数据展示 -->
      <DataDisplay />
      <!-- 历史数据图表 -->
      <div class="history-data card" ref="chart">
        <HistoryChart/>
      </div>
      <!-- <div class="history-data2 card" ref="chart">
        <HistoryChart2/>
      </div> -->
      <!-- 视频播放器 -->
      <div class="layout">
        <Layout :videoList="videoList"></Layout>
      </div>
  </div>
  </div>
</template>

<script>
import DataDisplay from './components/DataDisplay.vue';
import HistoryChart from './components/HistoryChart.vue';
import HistoryChart2 from './components/HistoryChart2.vue';
import VideoPlayer from './components/VideoPlayer.vue';
import Layout from './components/Layout.vue';
import screenfull from "screenfull";

export default {
  components: {
    DataDisplay,
    HistoryChart,
    HistoryChart2,
    VideoPlayer,
    Layout
  },
  mounted() {
    document.title = '番茄可视化'; 
  },
  data() {
    return {
      inputValue: 0,
      inputs: [
        { label: '种植面积', value: 0, placeholder: '请输入数字' },
        { label: '种植密度', value: 0, placeholder: '请输入数字' },
        { label: '光照强度', value: 0, placeholder: '请输入数字' },
        { label: '采收速率', value: 0, placeholder: '请输入数字' },
        { label: '采收密度', value: 0, placeholder: '请输入数字' },
        { label: '留果数量', value: 0, placeholder: '请输入数字' },
      ],
      chart2Data: [300, 200, 150, 80, 70, 230, 130],
      videoList: [
        { url: 'https://gcalic.v.myalicdn.com/gc/wgw05_1/index.m3u8?contentid=2820180516001' }, // 示例视频 1
        { url: 'https://gcalic.v.myalicdn.com/gc/wgw05_1/index.m3u8?contentid=2820180516001' }, // 示例视频 2
        { url: 'https://gcalic.v.myalicdn.com/gc/wgw05_1/index.m3u8?contentid=2820180516001' } // 示例视频 3
      ]
    };
  },

  computed: {
    outputValue() {
      return this.inputs.reduce((sum, input) => sum + input.value, 0);
    }
  },
  methods: {
    async goFullscreen() {
      if (screenfull.isEnabled) {
        try {
          if(screenfull.isFullscreen){
            await screenfull.exit();
          }else{
            await screenfull.request(); 
          }
        } catch (error) {
          console.error('全屏请求失败:', error);
        }
      } else {
        console.log('当前浏览器不支持全屏模式');
      }
    },
  }
};
</script>

<style>
/* 整体布局 */
.dashboard {
  width: 1900px;
  height: 1800px;
  background: linear-gradient(135deg, #38d33d 0%, #5086eb 100%);
  padding-top: 20px;
  box-sizing: border-box;
  position: relative; /* 为绝对定位提供上下文 */
}

.main-title {
  text-align: center;
  font-size: 2.5em; /* 字体大小 */
  color: white;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5); /* 文字阴影效果 */
  font-family: 'Arial', sans-serif; /* 使用无衬线字体 */
  margin-bottom: 20px; /* 底部外边距 */
  position: absolute; /* 绝对定位 */
  top: 0; /* 放置在顶部 */
  left: 0;
  right: 0;
  z-index: 10; /* 确保标题在其他内容之上 */
}

.sub-title {
  text-align: center;
  font-size: 2em; /* 子标题字体大小 */
  color: white;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5); /* 文字阴影效果 */
  font-family: 'Arial', sans-serif; /* 使用无衬线字体 */
  margin-bottom: 1cm; /* 底部外边距 */
  padding-top: 40px;
}

.content {
  display: grid;
  grid-template-rows: uto auto auto 1fr 1fr; /* 第一行自适应高度 */
  grid-template-columns: 1fr 1fr 1fr; /* 左中右三列 */
  gap: 20px;
  padding-top: 80px; /* 根据标题的高度调整，确保内容不被遮挡 */
}

.card {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
  font-size: 18px;
  animation: fade-in 1s;
}

.input-side {
  grid-row: 1; /* 第一行 */
  grid-column: 1; /* 第一列 */
  width: 60%;
  height: 70%;
  margin-left: 170px;
  margin-right: 0px;
  margin-top: 50px;
  margin-bottom: 50px;
}

/* 输入区域样式 */
.input-group {
  display: flex;
  justify-content: space-between; /* 水平分布 */
  align-items: center; /* 垂直居中对齐 */
  margin-bottom: 20px; /* 调整上下间距 */
  width: 100%; /* 确保输入组占据整个容器宽度 */

   /* 控制标签和输入框之间的间距 */
}

.input-group label {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.8);
  margin-right: 10px; /* 标签和输入框之间的间距 */
  margin-left: 10px;
  min-width: 100px; /* 固定标签宽度，保持对齐 */
  text-align: right; /* 标签右对齐 */
}

.input-group .el-input-number {
  width: 100px; /* 调整输入框宽度 */
}

.center-image {
  grid-row: 1; /* 第一行 */
  grid-column: 2; /* 第二列 */
  width: 800px;
  height: 445px;
}

.output-side {
  grid-row: 1; /* 第一行 */
  grid-column: 3; /* 第三列 */
  margin-left: 10px;
  width: 60%;
  height: 70%;
  margin-right: 0px;
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: 20px;
  margin-left: -6px;
}

.data-display {
  grid-row: 2; /* 第二行 */
  grid-column: 1 / span 3; /* 占据三列 */
}

.history-data {
  height: auto;
  grid-row: 3; /* 第三行 */
  grid-column: 1 / span 3; /* 占据三列 */
}

.history-data2 {
  grid-row: 4; /* 第四行 */
  grid-column: 1 / span 3; /* 占据三列 */
}

.center-image img {
  max-width: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.layout{
  grid-row: 5; /* 第五行 */
  grid-column: 1 / span 1; /* 占据三列 */
  width: 372%;
}

.data-grid {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.data-item {
  background: rgba(255, 255, 255, 0.2);
  padding: 20px;
  border-radius: 10px;
  margin: 10px;
  font-size: 20px;
  color: white;
  text-align: center;
}

.data-label {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8);
}

.data-value {
  font-size: 24px;
  font-weight: bold;
  margin-top: 5px;
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
</style>