<template>
    <div class="history-data2 card" ref="chart"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    mounted() {
      this.initChart2();
    },
    methods: {
      initChart2() {
        const chart = echarts.init(this.$refs.chart);
        chart.setOption({
          title: {
            text: '历史数据2',
            left: 'center',
            textStyle: {
              color: 'white'
            }
          },
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            type: 'category',
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
            axisLabel: {
              color: 'rgba(255, 255, 255, 0.7)'
            }
          },
          yAxis: {
            type: 'value',
            yAxisMin: 100, // 设置最小值为0，确保数据不会超出图表范围
            yAxisMax: 400, // 设置最大值为200，确保数据不会超出图表范围
            axisLabel: {
              color: 'rgba(255, 255, 255, 0.7)'
            }
          },
          series: [{
            data: [300, 100, 150, 400, 20, 80, 160],
            type: 'line',
            lineStyle: {
              color: '#FFA726',
              shadowColor: 'rgba(0, 0, 0, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 10
            },
            itemStyle: {
              color: '#FFA726'
            }
          }]
        });
      }
    }
  };
  </script>
  
  <style scoped>
  .history-data2 {
    width: 100%;
    height: 300px;
  }
  </style>